import {Component, OnInit} from "@angular/core";
import {ActivatedRoute, Router} from "@angular/router";
import {UserService} from "../user.service";
import {FormGroup, FormBuilder, Validators} from "@angular/forms";
import {numberBetween} from "../../common/validator/number-between-validator.directive";
import {User} from "../user";

@Component({
  selector: 'app-user-edit',
  templateUrl: './user-edit.component.html',
  styleUrls: ['./user-edit.component.css']
})
export class UserEditComponent implements OnInit {
  userForm:FormGroup;
  constructor(private route:ActivatedRoute,private userService:UserService,private fb:FormBuilder,private router:Router) { }

  ngOnInit() {
    this.userForm=this.fb.group({
      'userName':['',[Validators.required,Validators.minLength(4)]],
      'userAge':['',[Validators.required,numberBetween(1,100)]],
    });
    this.userForm.valueChanges.subscribe(data => this.onValueChanged(data));
  }


  addUser(user:User) {
    this.userService.addUser(user).subscribe(result=>this.router.navigate(['/user','list']));
  }

  onValueChanged(data) {
    for (const field in this.formErrors) {
      this.formErrors[field] = '';
      //åå°è¡¨åå­æ®µ
      const control = this.userForm.get(field);
      //è¡¨åå­æ®µå·²ä¿®æ¹ææ æ
      if (control && control.dirty && !control.valid) {
        //ååºå¯¹åºå­æ®µå¯è½çéè¯¯ä¿¡æ¯
        const messages = this.validationMessages[field];
        //ä»errorséååºéè¯¯ç±»åï¼åæ¼ä¸è¯¥éè¯¯å¯¹åºçä¿¡æ¯
        for (const key in control.errors) {
          this.formErrors[field] += messages[key] + '';
        }
      }

    }
  }
  //å­å¨éè¯¯ä¿¡æ¯
  formErrors = {
    'userName': '',
    'userAge':'',
    'sex':''
  };
  //éè¯¯å¯¹åºçæç¤º
  validationMessages = {
    'userName': {
      'required': 'ç¨æ·åå¿å¡«.',
      'minlength': 'ç¨æ·åå¤ªç­',
    },
    'userAge':{
      'required': 'è¯·è¾å¥å¹´é¾',
      'numberBetween': 'å¹´é¾èå´ä¸æ­£ç¡®',
    },
    'passwordGroup.password2':{
      'required': 'è¯·éå¤è¾å¥å¯ç ',
      'minlength': 'å¯ç å¤ªç­',
      'passwordNEQ':'ä¸¤æ¬¡è¾å¥å¯ç ä¸å',
      'password1InValid':''
    },
    'sex':{
      'required':'æ§å«å¿å¡«'
    }

  };

}



